.planets {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.planet-card {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column-reverse;
  font-size: 25px;
  margin: 10px 0 20px 0;
}

.planet-card {
  transition: 2s;
}

.planet-card:hover {
  transform: scale(1.5);
  transition: 2s;
}

.planet-image {
  width: 146px;
}

@media (prefers-reduced-motion: no-preference) {
  .planet-image{
    animation: App-planet-spin infinite 20s linear;
  }
}

@keyframes App-planet-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

